import {useQuery} from 'react-query'
import { ReactQueryDevtools } from 'react-query/devtools'
function Example() {
  const info = useQuery("repoData", () =>
    fetch(
      "https://6cfa6qks29.execute-api.eu-central-1.amazonaws.com/dev/getdata"
    ).then((res) => res.json())
  );

  console.log(info)

  return (
    <div>
      <h1>This data is served by a lambda function in aws connected to a DynamoDB database, cool right?</h1>
      <p>{info.data != undefined ? info.data[4].text : 'cargando '}</p>
      <p>{info.data != undefined ? info.data[1].text : 'cargando ' }</p>
    </div>
  );
}

export default Example
